@media (min-width: 600px) {
    nav {
        display: flex;
        background-color: rgb(20, 21, 26);
        
    }
.nav-container {
    width: 100%;
    display: flex;
    justify-content: right;
    align-items: center;

   box-shadow: 0px 10px 10px -1px rgb(41, 49, 85)
}

.nav-menu {
    display: flex;
    font-size: 18px;
    padding-right: 40px;
}

.nav-menu ol {
    height: 100%;
    display: flex;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr;
    justify-content: space-between;
}
.nav-menu ol li {
    padding: 20px;
    padding-right: 100px;
    text-decoration: none;
    list-style: none;
}

li a{
    text-decoration: none;
    color: white;
    font-weight: bold;
}
li a:hover{
    color: rgb(75, 152, 216);
}
}
@media (max-width: 599px) {
    nav {
        display: flex;
        background-color: rgb(20, 21, 26);
        
    }
    .nav-container {
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: right;
        align-items: center;
    
       box-shadow: 0px 10px 10px -1px rgb(41, 49, 85)
    }
  
    .nav-menu {
        display: flex;
        font-size: 18px;
        padding-right: 40px;
    }
    
    .nav-menu ol {
        height: 100%;
        display: flex;
        grid-template-columns: 1fr 1fr 1fr;
        grid-template-rows: 1fr;
        justify-content: space-between;
    }
    .nav-menu ol li {
        padding: 5px;
        text-decoration: none;
        list-style: none;
    }
    
    li a{
        text-decoration: none;
        color: white;
        font-weight: bold;
    }
    li a:hover{
        color: rgb(75, 152, 216);
    }
}