.container {
   
    width: 700px;
    height: 100%;
    display: block;
}
.title {
    font-weight: bold ;
    text-align: center;
    color: white;

}
.staking-calculator-section{
    padding-top: 30px;
    padding-bottom: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.calculator {
    display: block;
    height: auto;
    width: auto;
    padding: 2rem;
    align-items: center;
    justify-content: center;
    box-shadow: 0 20px 60px rgb(41, 49, 85);
    ;
}

.input-calculator {
    display:inline-block;
    width: 100%;
    height: 100%;
}
label {
    display: block;
    padding: 20px;
    font-weight: bold;
    font-size: 18px;
    color: white;
}
input {
    display: block;
    width: 100%;
    color: #201f46;
    background-color: #f7f6fb;
    border-radius: 5px;
    border: none;
    height: 2.2rem;
}

.button-name {
    text-align: center;
}
.button {
    font-size: 30px;
    background-color: rgb(20, 21, 26);
    box-shadow: 0px 10px 10px -6px  rgb(41, 49, 85);
    ;
    color: white;
}
.button:hover {
    color: rgb(75, 152, 216);
    cursor: pointer;
}